<template>
    <div>
        <v-row>
            <v-col class="d-flex justify-center">
                <v-card elevation="2" :color="firstWeaponSlot.weaponCardColor" width="200" height="200" @mouseover="mouseoverWeaponTab" @mouseout="mouseoutWeaponTab" @mouseup="mouseupWeaponTab">
                    <!-- <v-card-title style="word-break: normal; font-size: 16pt; --font-weight: 500">{{ firstWeaponTitle }}</v-card-title> -->
                    <v-card-title style="padding: 0"><v-spacer></v-spacer><v-btn icon @click="deleteWeapon()"><v-icon>mdi-close</v-icon></v-btn></v-card-title>
                    <v-card-text style="word-break: normal; font-size: 22pt; --font-weight: 500; line-height: 1.1;" class="d-flex justify-center align-center mt-5">{{ firstWeaponSlot.weaponTitle }}</v-card-text>
                </v-card>
            </v-col>
        </v-row>
        <v-row>
            <v-col class="d-flex justify-center">
                <v-card :color="firstWeaponSlot.firstComponentCardColor" elevation="2" width="150" height="150" @mouseover="mouseover_component_1 = true" @mouseout="mouseover_component_1 = false" @mouseup="mouseupComponentTab(1)">
                    <v-card-title style="padding: 0"><v-spacer></v-spacer><v-btn icon @click="deleteComponent(1)"><v-icon>mdi-close</v-icon></v-btn></v-card-title>
                    <v-card-text id="10" style="word-break: normal; font-size: 15pt; --font-weight: 500" class="d-flex justify-center align-center mt-5">{{ firstWeaponSlot.firstComponentLabel }}</v-card-text>
                </v-card>
                </v-col>
                <v-col class="d-flex justify-center">
                <v-card :color="firstWeaponSlot.secondComponentCardColor" elevation="2" width="150" height="150" @mouseover="mouseover_component_2 = true" @mouseout="mouseover_component_2 = false" @mouseup="mouseupComponentTab(2)">
                    <v-card-title style="padding: 0"><v-spacer></v-spacer><v-btn icon @click="deleteComponent(2)"><v-icon>mdi-close</v-icon></v-btn></v-card-title>
                    <v-card-text id="11" style="word-break: normal; font-size: 15pt; --font-weight: 500" class="d-flex justify-center align-center mt-5">{{ firstWeaponSlot.secondComponentLabel }}</v-card-text>
                </v-card>
                </v-col>
                <v-col class="d-flex justify-center">
                <v-card :color="firstWeaponSlot.thirdComponentCardColor" elevation="2" width="150" height="150" @mouseover="mouseover_component_3 = true" @mouseout="mouseover_component_3 = false" @mouseup="mouseupComponentTab(3)">
                    <v-card-title style="padding: 0"><v-spacer></v-spacer><v-btn icon @click="deleteComponent(3)"><v-icon>mdi-close</v-icon></v-btn></v-card-title>
                    <v-card-text id="12" style="word-break: normal; font-size: 15pt; --font-weight: 500" class="d-flex justify-center align-center mt-5">{{ firstWeaponSlot.thirdComponentLabel }}</v-card-text>
                </v-card>
                </v-col>
                <v-col class="d-flex justify-center">
                <v-card :color="firstWeaponSlot.fourthComponentCardColor" elevation="2" width="150" height="150" @mouseover="mouseover_component_4 = true" @mouseout="mouseover_component_4 = false" @mouseup="mouseupComponentTab(4)">
                    <v-card-title style="padding: 0"><v-spacer></v-spacer><v-btn icon @click="deleteComponent(4)"><v-icon>mdi-close</v-icon></v-btn></v-card-title>
                    <v-card-text id="13" style="word-break: normal; font-size: 15pt; --font-weight: 500" class="d-flex justify-center align-center mt-5">{{ firstWeaponSlot.fourthComponentLabel }}</v-card-text>
                </v-card>
                </v-col>
                <v-col class="d-flex justify-center">
                <v-card :color="firstWeaponSlot.fifthComponentCardColor" elevation="2" width="150" height="150" @mouseover="mouseover_component_5 = true" @mouseout="mouseover_component_5 = false" @mouseup="mouseupComponentTab(5)">
                    <v-card-title style="padding: 0"><v-spacer></v-spacer><v-btn icon @click="deleteComponent(5)"><v-icon>mdi-close</v-icon></v-btn></v-card-title>
                    <v-card-text id="14" style="word-break: normal; font-size: 15pt; --font-weight: 500" class="d-flex justify-center align-center mt-5">{{ firstWeaponSlot.fifthComponentLabel }}</v-card-text>
                </v-card>
            </v-col>
        </v-row>
    </div>
</template>

<script>
import alt from "@/main.js"

export default {
  name: "WeaponTab",
  props: ["tabIndex", "items"],
  data: () => ({
    /*
    items: [
        {
            "index": 0,
            "scriptName": "handcuffs",
            "label": "Kajdanki",
            "amount": 5
        },
        {
            "index": 1,
            "scriptName": "firstAidKit",
            "label": "Apteczka pierwszej pomocy",
            "amount": 3
        },
        {
            "index": 2,
            "scriptName": "handcuffs2",
            "label": "Kajdany zespolone",
            "amount": 1
        },
        {
            "index": 3,
            "scriptName": "weapon_pistol",
            "label": "Pistolet",
            "amount": 1
        },
        {
            "index": 4,
            "scriptName": "weaponComponent_suppressor",
            "label": "Tłumik",
            "amount": 1
        },
        {
            "index": 5,
            "scriptName": "weaponComponent_extendedMag",
            "label": "Powiększony magazynek",
            "amount": 1
        },
        {
            "index": 6,
            "scriptName": "weaponComponent_redDot",
            "label": "Celownik 'Red Dot'",
            "amount": 1
        },
        {
            "index": 7,
            "scriptName": "weaponComponent_holoSight",
            "label": "Celownik holograficzny",
            "amount": 1
        },
        {
            "index": 8,
            "scriptName": "weaponComponent_grip",
            "label": "Uchwyt poziomy",
            "amount": 1
        },
        {
            "index": 9,
            "scriptName": "weapon_carbinerifle_mk2",
            "label": "Karabinek mk. 2",
            "amount": 1
        },
    ],
    */
    mouseover_0: false,
    draggedItem: null,
    isTabActive: false,
    activeTab: null,
    firstWeaponSlot: {
        weaponTitle: "Broń",
        weaponScriptName: "",
        weaponCardColor: "grey lighten-2",
        firstComponentCardColor: "grey lighten-2",
        firstComponentLabel: "Magazynek",
        firstComponentScriptName: "",
        secondComponentCardColor: "grey lighten-2",
        secondComponentLabel: "Urządzenie wylotowe",
        secondComponentScriptName: "",
        thirdComponentCardColor: "grey lighten-2",
        thirdComponentLabel: "Latarka",
        thirdComponentScriptName: "",
        fourthComponentCardColor: "grey lighten-2",
        fourthComponentLabel: "Przyrządy celownicze",
        fourthComponentScriptName: "",
        fifthComponentCardColor: "grey lighten-2",
        fifthComponentLabel: "Uchwyt",
        fifthComponentScriptName: "",
    },
    mouseover_component_1: false,
    mouseover_component_2: false,
    mouseover_component_3: false,
    mouseover_component_4: false,
    mouseover_component_5: false,
    componentsInUse: [],
    weaponsInUse: [],
  }),

    methods: {
        dropWeapon(e) {
            const itemID = e.dataTransfer.getData("itemID")
            const item = document.getElementById(itemID)
            const itemScriptName = this.items[item.id].scriptName.toString()
            //console.log(item.id)
            //console.log(this.items[item.id].scriptName)

            if (itemScriptName.startsWith("weapon_")) {
                this.firstWeaponSlot.weaponTitle = this.items[item.id].label
                this.firstWeaponSlot.weaponCardColor = "green lighten-3"
            }
        },

        dropComponent1(e) {
            const itemID = e.dataTransfer.getData("itemID")
            const item = document.getElementById(itemID)
            const itemScriptName = this.items[item.id].scriptName.toString()

            //console.log(itemScriptName)
        },

        mouseoverWeaponTab() {
            this.mouseover_0 = true
        },

        mouseoutWeaponTab() {
            this.mouseover_0 = false
        },

        mouseupWeaponTab() {
            if (this.mouseover_0 === true && this.isTabActive === true) {
                setTimeout(() => {
                    if (this.draggedItem !== null) {
                        const itemScriptName = this.items[this.draggedItem.id].scriptName.toString()
                        if (itemScriptName.startsWith("weapon_") && this.canBeWeaponUsed(itemScriptName)) {
                            if (this.firstWeaponSlot.weaponScriptName !== "") {
                                this.$root.$emit('removeWeaponFromUse', this.firstWeaponSlot.weaponScriptName)
                                alt.emit("inv_hud:weaponRemoved", this.firstWeaponSlot.weaponScriptName)
                            }
                            this.firstWeaponSlot.weaponCardColor = "green lighten-3"
                            this.firstWeaponSlot.weaponTitle = this.items[this.draggedItem.id].label
                            this.draggedItem = null
                            this.firstWeaponSlot.weaponScriptName = itemScriptName
                            alt.emit("inv_hud:weaponEquiped", itemScriptName)
                            this.$root.$emit('setWeaponInUse', itemScriptName)
                        } else {
                            this.draggedItem = null
                            this.$root.$emit('clearDraggedItem')
                        }
                    }
                }, 70)
            }
        },

        mouseupComponentTab(componentCardId) {
            switch(componentCardId) {
                case 1:
                    if (this.mouseover_component_1 === true && this.isTabActive === true) {
                        setTimeout(() => {
                            if (this.draggedItem !== null) {
                                const itemScriptName = this.items[this.draggedItem.id].scriptName.toString()
                                if (itemScriptName.startsWith("weaponComponent_magazine") && this.canBeComponentUsed(this.items[this.draggedItem.id]) === true) {
                                    if (this.firstWeaponSlot.firstComponentScriptName !== "") {
                                        alt.emit("inv_hud:weaponComponentRemoved", this.firstWeaponSlot.weaponScriptName, this.firstWeaponSlot.firstComponentScriptName)
                                        this.$root.$emit('removeComponentFromUse', this.firstWeaponSlot.firstComponentScriptName)
                                    }
                                    this.firstWeaponSlot.firstComponentCardColor = "green lighten-3"
                                    this.firstWeaponSlot.firstComponentLabel = this.items[this.draggedItem.id].label
                                    this.firstWeaponSlot.firstComponentScriptName = itemScriptName
                                    this.draggedItem = null
                                    alt.emit("inv_hud:weaponComponentEquiped", this.firstWeaponSlot.weaponScriptName, itemScriptName)
                                    this.$root.$emit('setComponentInUse', itemScriptName)
                                } else {
                                    this.draggedItem = null
                                }
                            }
                        }, 70)
                    }
                    break;

                case 2:
                    if (this.mouseover_component_2 === true && this.isTabActive === true) {
                        setTimeout(() => {
                            if (this.draggedItem !== null) {
                                const itemScriptName = this.items[this.draggedItem.id].scriptName.toString()
                                if (this.canBeComponentUsed(this.items[this.draggedItem.id]) === true && itemScriptName == "weaponComponent_suppressor" || itemScriptName === "weaponComponent_muzzle_break" || itemScriptName === "weaponComponent_compensator") {
                                    if (this.firstWeaponSlot.firstComponentScriptName !== "") {
                                        alt.emit("inv_hud:weaponComponentRemoved", this.firstWeaponSlot.weaponScriptName, this.firstWeaponSlot.secondComponentScriptName)
                                        this.$root.$emit('removeComponentFromUse', this.firstWeaponSlot.secondComponentScriptName)
                                    }
                                    this.firstWeaponSlot.secondComponentCardColor = "green lighten-3"
                                    this.firstWeaponSlot.secondComponentLabel = this.items[this.draggedItem.id].label
                                    this.firstWeaponSlot.secondComponentScriptName = itemScriptName
                                    this.draggedItem = null
                                    alt.emit("inv_hud:weaponComponentEquiped", this.firstWeaponSlot.weaponScriptName, itemScriptName)
                                    this.$root.$emit('setComponentInUse', itemScriptName)
                                } else {
                                    this.draggedItem = null
                                }
                            }
                        }, 70)
                    }
                    break;

                case 3:
                    if (this.mouseover_component_3 === true && this.isTabActive === true) {
                        setTimeout(() => {
                            if (this.draggedItem !== null) {
                                const itemScriptName = this.items[this.draggedItem.id].scriptName.toString()
                                if (itemScriptName.startsWith("weaponComponent_flashlight") && this.canBeComponentUsed(this.items[this.draggedItem.id]) === true) {
                                    if (this.firstWeaponSlot.firstComponentScriptName !== "") {
                                        alt.emit("inv_hud:weaponComponentRemoved", this.firstWeaponSlot.weaponScriptName, this.firstWeaponSlot.thirdComponentScriptName)
                                        this.$root.$emit('removeComponentFromUse', this.firstWeaponSlot.thirdComponentScriptName)
                                    }
                                    this.firstWeaponSlot.thirdComponentCardColor = "green lighten-3"
                                    this.firstWeaponSlot.thirdComponentLabel = this.items[this.draggedItem.id].label
                                    this.firstWeaponSlot.thirdComponentScriptName = itemScriptName
                                    this.draggedItem = null
                                    alt.emit("inv_hud:weaponComponentEquiped", this.firstWeaponSlot.weaponScriptName, itemScriptName)
                                    this.$root.$emit('setComponentInUse', itemScriptName)
                                } else {
                                    this.draggedItem = null
                                }
                            }
                        }, 70)
                    }
                    break;

                case 4:
                    if (this.mouseover_component_4 === true && this.isTabActive === true) {
                        setTimeout(() => {
                            if (this.draggedItem !== null) {
                                const itemScriptName = this.items[this.draggedItem.id].scriptName.toString()
                                if (itemScriptName.startsWith("weaponComponent_scope") && this.canBeComponentUsed(this.items[this.draggedItem.id]) === true) {
                                    if (this.firstWeaponSlot.firstComponentScriptName !== "") {
                                        alt.emit("inv_hud:weaponComponentRemoved", this.firstWeaponSlot.weaponScriptName, this.firstWeaponSlot.fourthComponentScriptName)
                                        this.$root.$emit('removeComponentFromUse', this.firstWeaponSlot.fourthComponentScriptName)
                                    }
                                    this.firstWeaponSlot.fourthComponentCardColor = "green lighten-3"
                                    this.firstWeaponSlot.fourthComponentLabel = this.items[this.draggedItem.id].label
                                    this.firstWeaponSlot.fourthComponentScriptName = itemScriptName
                                    this.draggedItem = null
                                    alt.emit("inv_hud:weaponComponentEquiped", this.firstWeaponSlot.weaponScriptName, itemScriptName)
                                    this.$root.$emit('setComponentInUse', itemScriptName)
                                } else {
                                    this.draggedItem = null
                                }
                            }
                        }, 70)
                    }
                    break;

                case 5:
                    if (this.mouseover_component_5 === true && this.isTabActive === true) {
                        setTimeout(() => {
                            if (this.draggedItem !== null) {
                                const itemScriptName = this.items[this.draggedItem.id].scriptName.toString()
                                if (itemScriptName.startsWith("weaponComponent_") && this.canBeComponentUsed(this.items[this.draggedItem.id]) === true) {
                                    if (this.firstWeaponSlot.firstComponentScriptName !== "") {
                                        alt.emit("inv_hud:weaponComponentRemoved", this.firstWeaponSlot.weaponScriptName, this.firstWeaponSlot.fifthComponentScriptName)
                                        this.$root.$emit('removeComponentFromUse', this.firstWeaponSlot.fifthComponentScriptName)
                                    }
                                    this.firstWeaponSlot.fifthComponentCardColor = "green lighten-3"
                                    this.firstWeaponSlot.fifthComponentLabel = this.items[this.draggedItem.id].label
                                    this.firstWeaponSlot.fifthComponentScriptName = itemScriptName
                                    this.draggedItem = null
                                    alt.emit("inv_hud:weaponComponentEquiped", this.firstWeaponSlot.weaponScriptName, itemScriptName)
                                    this.$root.$emit('setComponentInUse', itemScriptName)
                                } else {
                                    this.draggedItem = null
                                }
                            }
                        }, 70)
                    }
                    break;
            }
        },

        deleteComponent(component) {
            switch (component) {
                case 1:
                    alt.emit("inv_hud:weaponComponentRemoved", this.firstWeaponSlot.weaponScriptName, this.firstWeaponSlot.firstComponentScriptName)
                    this.$root.$emit('removeComponentFromUse', this.firstWeaponSlot.firstComponentScriptName)
                    this.firstWeaponSlot.firstComponentCardColor = "grey lighten-2"
                    this.firstWeaponSlot.firstComponentLabel = "Magazynek"
                    this.firstWeaponSlot.firstComponentScriptName = ""
                    break;

                case 2:
                    alt.emit("inv_hud:weaponComponentRemoved", this.firstWeaponSlot.weaponScriptName, this.firstWeaponSlot.secondComponentScriptName)
                    this.$root.$emit('removeComponentFromUse', this.firstWeaponSlot.secondComponentScriptName)
                    this.firstWeaponSlot.secondComponentCardColor = "grey lighten-2"
                    this.firstWeaponSlot.secondComponentLabel = "Urządzenie wylotowe"
                    this.firstWeaponSlot.secondComponentScriptName = ""
                    break;

                case 3:
                    alt.emit("inv_hud:weaponComponentRemoved", this.firstWeaponSlot.weaponScriptName, this.firstWeaponSlot.thirdComponentScriptName)
                    this.$root.$emit('removeComponentFromUse', this.firstWeaponSlot.thirdComponentScriptName)
                    this.firstWeaponSlot.thirdComponentCardColor = "grey lighten-2"
                    this.firstWeaponSlot.thirdComponentLabel = "Latarka"
                    this.firstWeaponSlot.thirdComponentScriptName = ""
                    break;

                case 4:
                    alt.emit("inv_hud:weaponComponentRemoved", this.firstWeaponSlot.weaponScriptName, this.firstWeaponSlot.fourthComponentScriptName)
                    this.$root.$emit('removeComponentFromUse', this.firstWeaponSlot.fourthComponentScriptName)
                    this.firstWeaponSlot.fourthComponentCardColor = "grey lighten-2"
                    this.firstWeaponSlot.fourthComponentLabel = "Przyrządy celownicze"
                    this.firstWeaponSlot.fourthComponentScriptName = ""
                    break;

                case 5:
                    alt.emit("inv_hud:weaponComponentRemoved", this.firstWeaponSlot.weaponScriptName, this.firstWeaponSlot.fifthComponentScriptName)
                    this.$root.$emit('removeComponentFromUse', this.firstWeaponSlot.fifthComponentScriptName)
                    this.firstWeaponSlot.fifthComponentCardColor = "grey lighten-2"
                    this.firstWeaponSlot.fifthComponentLabel = "Uchwyt"
                    this.firstWeaponSlot.fifthComponentScriptName = ""
                    break;
            }
        },

        deleteWeapon() {
            this.$root.$emit('removeWeaponFromUse', this.firstWeaponSlot.weaponScriptName)
            alt.emit("inv_hud:weaponRemoved", this.firstWeaponSlot.weaponScriptName)
            this.firstWeaponSlot.weaponTitle = "Broń"
            this.firstWeaponSlot.weaponCardColor = "grey lighten-2"
            this.firstWeaponSlot.weaponScriptName = ""
        },

        canBeComponentUsed(component) {
            var componentToFind = this.componentsInUse.find((comp) => comp.componetScriptName === component.scriptName)
            if (componentToFind === undefined) {
                return true
            } else if (componentToFind !== undefined && componentToFind.amount < component.amount) {
                return true
            } else {
                return false
            }
        },

        canBeWeaponUsed(weapon) {
            var weaponToFind = this.weaponsInUse.find((weap) => weap === weapon.toString())
            if (weaponToFind === undefined) {
                return true
            } else {
                return false
            }
        },

        doesItemExists(item) {
            var itemToCheck = this.items.find((ite) => ite.scriptName === item)
            if (itemToCheck === undefined) {
                return false
            } else {
                return true
            }
        }
    },

    mounted() {
        this.$root.$on("draggedItem", (item) => {
            this.draggedItem = item
        });

        this.$root.$on("setActiveTab", (tab) => {
            if (parseInt(tab) === parseInt(this.tabIndex)) {
                this.isTabActive = true
                this.activeTab = parseInt(tab)
            } else {
                this.isTabActive = false
                this.activeTab = parseInt(tab)
            }
        });

        this.$root.$on("updateComponentsInUse", (componentsInUse) => {
            this.componentsInUse = componentsInUse
        });

        this.$root.$on("updateWeaponsInUse", (weaponsInUse) => {
            this.weaponsInUse = weaponsInUse
        });

        this.$root.$on("clearDraggedItem", (weaponsInUse) => {
            this.draggedItem = null
        });
    },

    created: 
        function() {
            if (parseInt(this.tabIndex) === 1) {
                this.isTabActive = true
            }

            if (parseInt(this.tabIndex) === this.activeTab) {
                this.isTabActive = true
            }
        },

    watch: {
        items: function(val) {
            var weaponExists = this.doesItemExists(this.firstWeaponSlot.weaponScriptName)
            var component_1Exists = this.doesItemExists(this.firstWeaponSlot.firstComponentScriptName)
            var component_2Exists = this.doesItemExists(this.firstWeaponSlot.secondComponentScriptName)
            var component_3Exists = this.doesItemExists(this.firstWeaponSlot.thirdComponentScriptName)
            var component_4Exists = this.doesItemExists(this.firstWeaponSlot.fourthComponentScriptName)
            var component_5Exists = this.doesItemExists(this.firstWeaponSlot.fifthComponentScriptName)

            if (!weaponExists) {
                this.$root.$emit('removeWeaponFromUse', this.firstWeaponSlot.weaponScriptName)
                alt.emit("inv_hud:weaponRemoved", this.firstWeaponSlot.weaponScriptName)
                this.firstWeaponSlot.weaponTitle = "Broń"
                this.firstWeaponSlot.weaponCardColor = "grey lighten-2"
                this.firstWeaponSlot.weaponScriptName = ""
            }

            if (!component_1Exists) {
                alt.emit("inv_hud:weaponComponentRemoved", this.firstWeaponSlot.firstComponentScriptName)
                this.$root.$emit('removeComponentFromUse', this.firstWeaponSlot.firstComponentScriptName)
                this.firstWeaponSlot.firstComponentCardColor = "grey lighten-2"
                this.firstWeaponSlot.firstComponentLabel = "Magazynek"
                this.firstWeaponSlot.firstComponentScriptName = ""    
            }

            if (!component_2Exists) {
                alt.emit("inv_hud:weaponComponentRemoved", this.firstWeaponSlot.secondComponentScriptName)
                this.$root.$emit('removeComponentFromUse', this.firstWeaponSlot.secondComponentScriptName)
                this.firstWeaponSlot.secondComponentCardColor = "grey lighten-2"
                this.firstWeaponSlot.secondComponentLabel = "Urządzenie wylotowe"
                this.firstWeaponSlot.secondComponentScriptName = ""  
            }

            if (!component_3Exists) {
                alt.emit("inv_hud:weaponComponentRemoved", this.firstWeaponSlot.thirdComponentScriptName)
                this.$root.$emit('removeComponentFromUse', this.firstWeaponSlot.thirdComponentScriptName)
                this.firstWeaponSlot.thirdComponentCardColor = "grey lighten-2"
                this.firstWeaponSlot.thirdComponentLabel = "Latarka"
                this.firstWeaponSlot.thirdComponentScriptName = ""
            }

            if (!component_4Exists) {
                alt.emit("inv_hud:weaponComponentRemoved", this.firstWeaponSlot.fourthComponentScriptName)
                this.$root.$emit('removeComponentFromUse', this.firstWeaponSlot.fourthComponentScriptName)
                this.firstWeaponSlot.fourthComponentCardColor = "grey lighten-2"
                this.firstWeaponSlot.fourthComponentLabel = "Przyrządy celownicze"
                this.firstWeaponSlot.fourthComponentScriptName = ""
            }

            if (!component_5Exists) {
                alt.emit("inv_hud:weaponComponentRemoved", this.firstWeaponSlot.fifthComponentScriptName)
                this.$root.$emit('removeComponentFromUse', this.firstWeaponSlot.fifthComponentScriptName)
                this.firstWeaponSlot.fifthComponentCardColor = "grey lighten-2"
                this.firstWeaponSlot.fifthComponentLabel = "Uchwyt"
                this.firstWeaponSlot.fifthComponentScriptName = ""
            }
        }
    }
}
</script>