  <v-app v-bind:style="{ display: display }">
    <v-row>
      <v-toolbar color="transparent" elevation="0">
        <!-- <v-toolbar-title class="ml-5">Ekwipunek</v-toolbar-title> -->
        <v-spacer></v-spacer>
        <v-btn class="mr-5" icon @click="hideHUD">
          <v-icon>mdi-close</v-icon>
