    <v-row class="mx-10" style="--width: 900px; margin-top: 20px">
      <v-col class="d-flex flex-wrap">
          <div v-for="item in items" class="d-flex flex-wrap" style="height: auto; width: auto;" v-bind:key="item.index">
            <!-- <draggable class="d-flex flex-wrap" group="items" @end="onDragEnd"> -->
              <Item v-bind:item="item" />
            <!-- </draggable> -->
          </div>
        <!-- <Item draggable="true" class="ml-5" v-for="item in items" v-bind:item="item" v-bind:key="item.index" /> -->
      </v-col>
      <v-col>
        <v-card>
          <v-card-title>Uzbrojenie</v-card-title>
          <!-- <v-card-subtitle class="float-left">Przeciągnij i upuść, aby ustawić broń i jej komponenty</v-card-subtitle> -->
          <v-card-text>
            <v-tabs fixed-tabs>
              <v-tab v-model="weaponTab_1" @change="setActiveTab(1)">Broń 1.</v-tab>
              <v-tab v-model="weaponTab_2" @change="setActiveTab(2)">Broń 2.</v-tab>
              <v-tab v-model="weaponTab_3" @change="setActiveTab(3)">Broń 3.</v-tab>
              <v-tab v-model="weaponTab_4" @change="setActiveTab(4)">Broń 4.</v-tab>
              <v-tab v-model="weaponTab_5" @change="setActiveTab(5)">Broń 5.</v-tab>
              <v-tab-item>
                <WeaponTab tabIndex="1" :items="items" />
              </v-tab-item>
              <v-tab-item>
                <WeaponTab tabIndex="2" :items="items" />
              </v-tab-item>
              <v-tab-item>
                <WeaponTab tabIndex="3" :items="items" />
              </v-tab-item>
              <v-tab-item>
                <WeaponTab tabIndex="4" :items="items" />
              </v-tab-item>
              <v-tab-item>
                <WeaponTab tabIndex="5" :items="items" />
              </v-tab-item>
            </v-tabs>
          </v-card-text>
        </v-card>
      </v-col>
      <!-- <div v-for="item in items" style="height: auto; width: auto;" v-bind:key="item.index" class="ml-5">
        <Item v-bind:item="item" />
      </div> -->
    </v-row>
  </v-app>
</template>

<script>
import Item from "./components/Item"
import WeaponTab from "./components/WeaponTab"
import draggable from "vuedraggable"
import alt from "@/main.js"

export default {
  name: 'App',

  components: {
    Item,
    WeaponTab,
    draggable
  },

  data: () => ({
    items: [
      /*
      {
        "index": 0,
        "scriptName": "handcuffs",
        "label": "Kajdanki",
        "amount": 5
      },
      {
        "index": 1,
        "scriptName": "firstAidKit",
        "label": "Apteczka pierwszej pomocy",
        "amount": 3
      },
      {
        "index": 2,
        "scriptName": "handcuffs2",
        "label": "Kajdany zespolone",
        "amount": 1
      },
      {
        "index": 3,
        "scriptName": "weapon_pistol",
        "label": "Pistolet",
        "amount": 1
      },
      {
        "index": 4,
        "scriptName": "weaponComponent_suppressor",
        "label": "Tłumik",
        "amount": 1
      },
      {
        "index": 5,
        "scriptName": "weaponComponent_magazine_extended",
        "label": "Powiększony magazynek",
        "amount": 1
      },
      {
        "index": 6,
        "scriptName": "weaponComponent_scope_holo",
        "label": "Celownik holograficzny",
        "amount": 1
      },
      {
        "index": 7,
        "scriptName": "weaponComponent_grip",
        "label": "Uchwyt poziomy",
        "amount": 1
      },
      {
        "index": 8,
        "scriptName": "weapon_carbinerifle_mk2",
        "label": "Karabinek mk. 2",
        "amount": 1
      },
      {
        "index": 9,
        "scriptName": "weapon_assaultrifle_mk2",
        "label": "Karabin szturmowy mk. 2",
        "amount": 1
      },      
      {
        "index": 10,
        "scriptName": "weapon_stungun",
        "label": "Paralizator",
        "amount": 2
      },
      {
        "index": 11,
        "scriptName": "weaponComponent_scope_large",
        "label": "Duża luneta",
        "amount": 2
      },
      */
    ],
    activeTab: null,
    display: "none",
    componentsInUse: [],
    weaponsInUse: [],
  }),

  methods: {
    
    setActiveTab(tab) {
      //console.log(tab)
      this.$root.$emit('setActiveTab', tab)
      this.activeTab = tab
    },

    hideHUD() {
      this.display = "none"
      alt.emit("inv_hud:hideHUD")
    }

  /*
    getItem(itemIndex) {
      var itemToFind = this.items.find((item) => item.index === itemIndex)

      if (itemToFind != undefined) {
        return itemToFind
      } else {
        return -1
      }
    }
    */
  },

  beforeMount() {
      if ("alt" in window) {
          window.alt.on("inv_hud:loadInventory", (inventory) => {
              this.items = inventory
          });
          window.alt.on("inv_hud:showInventory", () => {
              this.display = "flex"
          });

          window.alt.on("inv_hud:forceHideHUD", () => {
              this.hideHUD()
          });
      }
  },

  mounted() {
    this.$root.$on("setComponentInUse", (component) => {
      var componentInArray = this.componentsInUse.find((comp) => comp.componetScriptName === component.toString())
      if (componentInArray == undefined) {
        var obj = {componetScriptName: component, amount: 1}
        this.componentsInUse.push(obj)
        this.$root.$emit('updateComponentsInUse', this.componentsInUse)
      } else {
        componentInArray.amount++
        this.$root.$emit('updateComponentsInUse', this.componentsInUse)
      }
    });

    this.$root.$on("removeComponentFromUse", (component) => {
      var componentInArray = this.componentsInUse.find((comp) => comp.componetScriptName === component.toString())
      if (componentInArray == undefined) return
      if (componentInArray.amount > 1) {
        componentInArray.amount--
      } else {
        this.componentsInUse.splice(this.componentsInUse.indexOf(componentInArray), 1)
      }
      this.$root.$emit('updateComponentsInUse', this.componentsInUse)
    });


    this.$root.$on("setWeaponInUse", (weapon) => {
      var weapontInArray = this.weaponsInUse.find((weap) => weap === weapon.toString())
      if (weapontInArray === undefined) {
        this.weaponsInUse.push(weapon.toString())
        this.$root.$emit('updateWeaponsInUse', this.weaponsInUse)
      }
    });

    this.$root.$on("removeWeaponFromUse", (weapon) => {
      var weapontInArray = this.weaponsInUse.find((weap) => weap === weapon.toString())
      if (weapontInArray !== undefined) {
        this.weaponsInUse.splice(this.weaponsInUse.indexOf(weapontInArray), 1)
        this.$root.$emit('updateWeaponsInUse', this.weaponsInUse)
      }
    });
  }
};
</script>

