<template>
    <v-app>
        <draggable group="items" @end="onDragEnd" class="d-flex flex-wrap" :force-fallback="true">
            <v-badge :id="item.index" color="green" bottom overlap bordered :content="item.amount" offset-x="25" offset-y="25">
                <v-card :id="item.index" class="ml-5 mt-5" width="150" height="150" @contextmenu="show">
                    <!-- :img="require(`@/assets/${item.scriptName}.png`)" -->
                    <!-- draggable @dragstart="dragStart" -->
                    
                    <v-card-title primary-title class="text-wrap justify-center text-center" style="word-break: normal; --font-size: 13pt; --font-weight: 500">{{item.label}}</v-card-title>
                    
                    <!-- <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn icon @click="show">
                            <v-icon>mdi-dots-vertical</v-icon>
                        </v-btn>
                    </v-card-actions> -->
                </v-card>
            </v-badge>
        </draggable>
        <v-menu v-model="showMenu" :position-x="x" :position-y="y" absolute offset-y close-on-content-click close-on-click>
            <v-list>
                <v-list-item @click="useItemButton">
                    <v-list-item-title>Użyj</v-list-item-title>
                </v-list-item>
                <v-list-item @click="giveItemButton">
                    <v-list-item-title>Przekaż</v-list-item-title>
                </v-list-item>
                <v-list-item @click="throwOutItemButton">
                    <v-list-item-title>Wyrzuć</v-list-item-title>
                </v-list-item>
            </v-list>
        </v-menu>
        <v-dialog v-model="throwOutAmountDialog" max-width="250px">
            <v-card>
                <v-card-title>Ilość</v-card-title>
                <v-card-text>
                    <v-slider thumb-label="always" thumb-size="24" v-model="giveItemAmountSlider" :max="item.amount" min="1">
                        <template v-slot:prepend>
                            <v-icon @click="itemAmountSlider--">mdi-minus</v-icon>
                        </template>

                        <template v-slot:append>
                            <v-icon @click="itemAmountSlider++">mdi-plus</v-icon>
                        </template>
                    </v-slider>
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="red darken-1" text @click="throwOutAmountDialog = false">Anuluj</v-btn>
                    <v-btn color="green darken-1" text @click="throwOutItemDialogConfirm">Zatwierdź</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
        <v-dialog v-model="giveAmountDialog" max-width="250px">
            <v-card>
                <v-card-title>Ilość</v-card-title>
                <v-card-text>
                    <v-slider thumb-label="always" thumb-size="24" v-model="giveItemAmountSlider" :max="item.amount">
                        <template v-slot:prepend>
                            <v-icon @click="giveItemAmountSlider--">mdi-minus</v-icon>
                        </template>

                        <template v-slot:append>
                        <v-icon @click="giveItemAmountSlider++">mdi-plus</v-icon>
                        </template>
                    </v-slider>
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="red darken-1" text @click="giveAmountDialog = false">Anuluj</v-btn>
                    <v-btn color="green darken-1" text @click="giveItemDialogConfirm">Zatwierdź</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </v-app>
</template>

<script>
import draggable from "vuedraggable"
import alt from "@/main.js"

export default {
    name: "Item",
    props: ["item"],

    components: {
        draggable
    },

    data: () => ({
        showMenu: false,
        x: 0,
        y: 0,
        throwOutAmountDialog: false,
        giveAmountDialog: false,
        itemAmountSlider: 0,
        giveItemAmountSlider: 0
    }),

    methods: {
        show (e) {
            e.preventDefault()
            this.showMenu = false
            this.x = e.clientX
            this.y = e.clientY
            this.$root.$emit('closeOtherMenus')
            this.$nextTick(() => {
                this.showMenu = true
            })
        },

        useItemButton() {
            alt.emit("inv_hud:useItem", this.item.index, this.item.scriptName)
            //alert(`alt.emit("inv_hud:useItem", ${this.item.index}, ${this.item.scriptName})`)
        },
        
        throwOutItemButton() {
            if (this.item.amount === 1) {
                alt.emit("inv_hud:throwOutItem", this.item.index, this.item.scriptName, this.itemAmountSlider)
                //alert(`alt.emit("inv_hud:throwOutItem", ${this.item.index}, ${this.item.scriptName}, ${this.itemAmountSlider})`)
            } else {
                this.throwOutAmountDialog = true
            }
        },

        giveItemButton() {
            if (this.item.amount === 1) {
                alt.emit("inv_hud:giveItem", this.item.index, this.item.scriptName, this.itemAmountSlider)
                //alert(`alt.emit("inv_hud:giveItem", ${this.item.index}, ${this.item.scriptName}, ${this.item.amount})`)
            } else {
                this.giveAmountDialog = true
            }
        },

        throwOutItemDialogConfirm() {
            //alert(`${this.item.index}, ${this.item.scriptName}, ${this.itemAmountSlider}`)
            this.throwOutAmountDialog = false
        },

        giveItemDialogConfirm() {
            //alert(`alt.emit("inv_hud:giveItem", ${this.item.index}, ${this.item.scriptName}, ${this.giveItemAmountSlider})`)
            this.giveAmountDialog = false
        },

        dragStart(e) {
            const target = e.target;
            
            e.dataTransfer.setData("itemID", target.id)
        },

        onDragEnd(evt) {
            //console.log(evt);
            this.$root.$emit("draggedItem", evt.item)
        },
    },

    mounted() {
        this.$root.$on("closeOtherMenus", () => {
            this.showMenu = false
        });
    }
}
</script>

<style>
.v-application--wrap {
    min-height: auto;
}
</style>